<template>
  <navigation-wrapper :class="modeClass">
    <div class="l-navigation__content l-form">
      <div class="u-align-c">
        <div class="p-about__logo"></div>
      </div>
      <div class="p-about__app">{{ $t('about.app') }}</div>
      <div class="p-about__version">{{ version }}</div>
      <div class="p-about__content" v-html="content"></div>
      <div class="p-about__tip">{{ $t('about.tip') }}</div>
      <textarea class="p-about__feedback" :placeholder="$t('about.feedback')" v-model.trim="feedback"></textarea>
      <div class="o-button o-button--submit t-adapt-button--active" :class="{ 'is-active': feedback }"  @click="submit">{{ $t('common.submit') }}</div>
    </div>
    <div v-if="isConfirm" class="c-abount-mask" @touchmove.stop.prevent>
      <div class="c-abount-confirm">
        <div class="c-abount-confirm__title_wrapper">
          <div class="c-abount-confirm__title"><i :class="{ 'c-abount-confirm__success': !msg, 'c-abount-confirm__fail': msg }"></i>{{ title }}</div>
        </div>
        <div v-if="msg" class="c-abount-confirm__content">{{ msg }}</div>
        <div class="c-abount-confirm__wrapper">
          <div class="c-abount-confirm__button t-adapt-text" @click="isConfirm = false">{{ $t('common.confirm') }}</div>
        </div>
      </div>
    </div>
  </navigation-wrapper>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import * as SystemEvent from '@/code/system-event.js'

export default {
  name: 'about',
  data () {
    return {
      version: '',
      content: '',
      feedback: '',
      isConfirm: false,
      title: '',
      msg: ''
    }
  },
  computed: {
    ...mapState(['user']),
    ...mapGetters(['isStaff']),
    modeClass () {
      return this.isStaff ? 't-staff' : 't-parent'
    },
    url () {
      return this.isStaff ? 'staff/suggestion' : 'parent/suggestion'
    }
  },
  methods: {
    getVersion () {
      this.$native.getNativeAPPVer().then(version => {
        this.version = `${ this.$t('about.version') } ${ version }`
      })
    },
    submit () {
      if (this.feedback) {
        if (!this.$submitHttpPost) {
          this.$submitHttpPost = this.$http.createPost(this.url, {
            opts: {
              lock: true
            },
            success: () => {
              this.feedback = ''
              this.tip(true)
            },
            fail: () => {
              this.tip(false)
              return true
            }
          })
        }
        this.$submitHttpPost.request({
          token: this.user.token,
          content: this.feedback
        })
      }
    },
    tip (success) {
      this.isConfirm = true
      this.title = success ? this.$t('about.successTitle') : this.$t('about.failTitle')
      this.msg = success ? '' : this.$t('about.fail')
    }
  },
  created () {
    this.$eventBus.$on(SystemEvent.NATIVE_READY, this.getVersion)
    this.getVersion()
    this.$httpPost = this.$http.createPost('sundry/school/about/fetch', {
      opts: {
        retry: true
      },
      success: res => {
        this.content = this.$lang.isChinese() ? res.data.contentHK : res.data.content
      }
    })
    this.$httpPost.request({
      token: this.user.token
    })
  },
  beforeDestroy () {
    this.$eventBus.$off(SystemEvent.NATIVE_READY, this.getVersion)
    this.$httpPost.cancel()
    if (this.$submitHttpPost) {
      this.$submitHttpPost.cancel()
    }
  }
}
</script>

<style lang="scss">
.p-about {
  &__logo {
    display: inline-block;
    @include bg-img-ratio('../../assets/logo', 150, 150);
  }
  &__app {
    margin-top: 14px;
    color: #333;
    font-size: 20px;
    text-align: center;
    @extend %font-family__helvetica--bold;
  }
  &__version {
    margin-top: 15px;
    color: #333;
    font-size: 13px;
    text-align: center;
    @extend %font-family__pingfang;
  }
  &__content {
    margin-top: 50px;
    color: #333;
    font-size: 13px;
    text-align: left;
    line-height: 2;
    @extend %font-family__helvetica;
  }
  &__tip {
    padding-top: 10px;
    color: #333333;
    font-size: 13px;
    @extend %font-family__pingfang;
  }
  &__feedback {
    margin: 13px 0 16px;
    width: 100%;
    padding: 16px 10px;
    height: 130px;
    box-sizing: border-box;
    border: 1px solid rgba(#404040, 0.3);
    outline: none;
    color: #333333;
    font-size: 15px;
    resize: none;
    @extend %font-family__pingfang;
    &::-webkit-input-placeholder {
      color: rgba(#333333, 0.5);
      font-size: 15px;
      @extend %font-family__pingfang;
    }
  }
}
.c-abount-mask {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(#000, 0.7);
  z-index: 999;
}
.c-abount-confirm {
  position: fixed;
  top: 220px;
  left: 50%;
  width: 280px;
  margin-left: -140px;
  border-radius: 6px;
  background-color: #fff;
  &__title_wrapper {
    padding: 30px 0 22px;
    text-align: center;
  }
  &__title {
    padding-left: 40px;
    position: relative;
    display: inline-block;
    color: #333;
    font-size: 18px;
    @extend %font-family__helvetica--bold;
  }
  &__success {
    left: 0;
    @extend %vertical-middle;
    @include bg-img-ratio('../../assets/success', 48, 48);
  }
  &__fail {
    left: 0;
    @extend %vertical-middle;
    @include bg-img-ratio('../../assets/failed', 48, 48);
  }
  &__content {
    position: relative;
    padding: 0 20px 30px;
    color: rgba(#000, 0.5);
    font-size: 14px;
    text-align: center;
    @extend %font-family__pingfang;
  }
  &__wrapper {
    position: relative;
    font-size: 18px;
    height: 50px;
    @extend %font-family__helvetica;
    @include border-top-1px(#d2d3d5);
  }
  &__button {
    line-height: 50px;
    text-align: center;
  }
}
</style>
