import Vue from 'vue'
import md5 from 'js-md5'
import axios from 'axios'
import cfg, { setServerUrl, transformHeadUrl, transformUrl } from 'config'
import { log, setEnabled } from '@/utils/log.js'
import EventBus from '@/utils/event-bus.js'
import http from '@/utils/http.js'
import JsNativeApi, { inject } from '@/utils/js-native-api.js'
import { langs, lang, fetchErroMsg } from '@/locale/index.js'
import { listen } from '@/utils/silence.js'
import { methods } from '@/utils/bracelet.js'

Vue.config.productionTip = cfg.isDebug
Vue.config.debug = cfg.isDebug
Vue.config.devtools = cfg.isDebug

let base_url
let useLog
if (cfg.useBackDoor) {
  base_url = window.localStorage.getItem('smart_campus__base_url') || cfg.base_url
  let backDoorUseLog = window.localStorage.getItem('smart_campus__debug')
  if (backDoorUseLog) {
    useLog = backDoorUseLog === 'true'
  } else {
    useLog = cfg.useLog
  }
} else {
  base_url = cfg.base_url
  useLog = cfg.useLog
}
setServerUrl(base_url)
setEnabled(useLog)
axios.defaults.baseURL = base_url
axios.defaults.timeout = cfg.timeout
// axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded'
// axios.defaults.headers.post['Content-Type'] = 'application/json;charset=utf-8'

Vue.prototype.$cfg = cfg
Vue.prototype.$transformHeadUrl = transformHeadUrl
Vue.prototype.$transformUrl = transformUrl
Vue.prototype.$log = log
Vue.prototype.$eventBus = EventBus
Vue.prototype.$http = http
Vue.prototype.$native = JsNativeApi
Vue.prototype.$md5 = md5
Vue.prototype.$lang = lang
Vue.prototype.$langs = langs
Vue.prototype.$bracelet = methods

export default function initialize () {
  fetchErroMsg()
  listen()
  return inject()
}
