<template>
  <safe-area-layout class="l-operation p-unbind">
    <div slot="topbar" class="l-operation__title">{{ $t('unbind.title') }}</div>
    <div class="l-operation__header">{{ $t('unbind.tip') }}</div>
    <div class="l-operation__content u-align-c">
      <div class="p-unbind__img">
        <div class="p-unbind__avatar" :class="[avatarClass, { 'has-avatar': !!headUrl }]">
          <div v-if="!!headUrl" class="c-solid-avatar__icon">
            <i :style="{ backgroundImage: `url(${ headUrl })` }"></i>
          </div>
        </div>
        <div class="p-unbind__link"></div>
      </div>
    </div>
    <div slot="footer" class="l-operation__footer">
      <div class="l-operation__button o-border-button o-button--cancel" @click="cancel">{{ $t('common.cancel') }}</div>
      <div class="l-operation__button o-button o-button--prominent" @click="isConfirm = true">{{ fail ? $t('bracelet.retry') : $t('bracelet.unbind') }}</div>
    </div>
    <div v-if="isConfirm" class="c-unbind-mask" @touchmove.stop.prevent>
      <div class="c-unbind-confirm">
        <div class="c-unbind-confirm__title">{{ $t('common.tip') }}</div>
        <div class="c-unbind-confirm__content">{{ $t('unbind.confirm') }}</div>
        <div class="c-unbind-confirm__wrapper">
          <div class="c-unbind-confirm__button--cancel" @click="isConfirm = false">{{ $t('common.cancel') }}</div>
          <div class="c-unbind-confirm__button--ok t-adapt-text" @click="unbind">{{ $t('common.confirm') }}</div>
        </div>
      </div>
    </div>
  </safe-area-layout>
</template>

<script>
import { mapState, mapGetters, mapActions } from 'vuex'
import { Gender } from '@/enum/common.js'

export default {
  name: 'unbind',
  data () {
    return {
      fail: false,
      isConfirm: false
    }
  },
  computed: {
    ...mapState(['user']),
    ...mapGetters(['isStaff', 'getFigure']),
    url () {
      return this.isStaff ? 'staff/bracelet/unbind' : 'parent/bracelet/unbind'
    },
    id () {
      return Number(this.$route.params.id)
    },
    figure () {
      return this.getFigure(this.id)
    },
    avatarClass () {
      if (this.figure) {
        if (this.isStaff) {
          return this.figure.gender === Gender.MALE ? 'c-solid-avatar--male' : 'c-solid-avatar--female'
        } else {
          return this.figure.gender === Gender.MALE ? 'c-solid-avatar--boy' : 'c-solid-avatar--girl'
        }
      }
      return 'c-solid-avatar--male'
    },
    headUrl () {
      return this.figure && this.figure.headUrlAttachmentId ? this.$transformHeadUrl(this.figure.headUrlAttachmentId, this.user.token) : null
    }
  },
  methods: {
    ...mapActions(['updateFigureInfo', 'writeBracelet']),
    cancel () {
      this.$router.back()
    },
    unbind () {
      this.isConfirm = false
      if (!this.$httpPost) {
        this.$httpPost = this.$http.createPost(this.url, {
          opts: {
            lock: true
          },
          success: () => {
            this.writeBracelet({
              key: this.id,
              state: false
            })
            if (this.figure) {
              this.$native.unbindBracelet(this.isStaff ? this.figure.macAddress : this.figure.braceletMac)
            }
            this.updateFigureInfo({
              id: this.id,
              info: this.isStaff ? {
                braceletOfBinding: 0,
                macAddress: ''
              } : {
                braceletOfBinding: 0,
                braceletMac: ''
              }
            })
            this.cancel()
          },
          fail: () => {
            this.fail = true
          }
        })
      }
      this.$httpPost.request(this.isStaff ? {
        token: this.user.token
      } : {
        token: this.user.token,
        idStudent: this.id
      })
    }
  },
  beforeDestroy () {
    if (this.$httpPost) {
      this.$httpPost.cancel()
    }
  }
}
</script>

