import base from './base.js'

const base_url = 'https://tencentim.cdnis.edu.hk/icapp/'

const prod = Object.assign(base, {
  base_url
})

export default prod

let server_url = base_url

export function setServerUrl (serverUrl) {
  server_url = serverUrl
}

export function transformHeadUrl (attachmentId, token) {
  return `${ server_url }commont/getAttachment?attachmentId=${ attachmentId }&token=${ token }`
}

export function transformUrl (attachmentId) {
  return `${ server_url }common/getAttachment?attachmentId=${ attachmentId }`
}
