import cfg from 'config'
import { LoginMode } from '@/enum/common.js'
import * as Event from '@/code/custom-event.js'
import EventBus from '@/utils/event-bus.js'
import JsNativeApi from '@/utils/js-native-api.js'
import { log } from '@/utils/log.js'

let bgUrl = null
function savePic (url) {
  let canvas = document.createElement('canvas')
  let ctx = canvas.getContext('2d')
  let img = new Image()
  img.crossOrigin = 'Anonymous'
  img.onload = function () {
    if (url === bgUrl) {
      canvas.width = img.width
      canvas.height = img.height
      ctx.drawImage(img, 0, 0)
      EventBus.$emit(Event.SAVE_PIC_SUCCESS, url)
      try {
        window.localStorage.setItem('smart_campus__bg', canvas.toDataURL('image/jpg'))
        log(`bg load success: ${ url }`)
      } catch (e) {
        log(`bg save failed, ${ url }: ${ e.message }`)
      }
    }
    canvas = null
    ctx = null
    img = null
  }
  img.onerror = function () {
    if (url === bgUrl) {
      bgUrl = null
      log(`bg load failed: ${ url }`)
    }
    canvas = null
    ctx = null
    img = null
  }
  img.onabort = function () {
    if (url === bgUrl) {
      bgUrl = null
      log(`bg load abort: ${ url }`)
    }
    canvas = null
    ctx = null
    img = null
  }
  img.src = url
}

let upgradeTimer = -1

export function listen () {
  EventBus.$on(Event.SAVE_PIC, url => {
    if (bgUrl !== url) {
      bgUrl = url
      savePic(url)
    }
  })

  EventBus.$on(Event.LOGIN, user => {
    JsNativeApi.register(user.account)
    upgradeTimer = setTimeout(() => {
      JsNativeApi.updateH5({
        token: user.token,
        userType: user.mode === LoginMode.STAFF ? 1 : 2
      })
    }, cfg.upgrade)
  })

  EventBus.$on(Event.LOGOUT, () => {
    JsNativeApi.unregister()
    clearTimeout(upgradeTimer)
  })
}
