import * as SystemEvent from '@/code/system-event.js'
import * as Event from '@/code/custom-event.js'
import EventBus from '@/utils/event-bus.js'
import http from '@/utils/http.js'
import JsNativeApi from '@/utils/js-native-api.js'
import { log } from '@/utils/log.js'
import Store from '@/store/index.js'

export const State = {
  UNINITIALIZED: 'UNINITIALIZED',
  WAITING: 'WAITING',
  CONNECTING: 'CONNECTING',
  CONNECTED: 'CONNECTED',
  DISCONNECTING: 'DISCONNECTING',
  SYNCING: 'SYNCING'
}

let unique = 0
let currState = State.UNINITIALIZED
let currMac = null
let tempMac = null
let currEnerge = -1
let hasWritten = {}
let cache = {}

function findTarget (mac) {
  if (!Store.getters.isLogined) {
    return null
  }
  let isStaff = Store.getters.isStaff
  let figure
  if (isStaff) {
    figure = Store.state.figures.find(figure => figure.macAddress === mac)
  } else {
    figure = Store.state.figures.find(figure => figure.braceletMac === mac)
  }
  if (figure) {
    return {
      unique,
      mac,
      isStaff,
      token: Store.state.user.token,
      id: figure.id,
      key: isStaff ? figure.legacyId : figure.studentId,
      name: figure.name,
      isBraceletWritten: figure.isBraceletWritten
    }
  }
  return null
}

function hasChanged (info) {
  return info.unique !== unique
}

function isWhiteBracelet (mac) {
  switch (mac) {
    case 'E6762E90D410':
    case 'FC69D52DD033':
    case 'C52A53A61A82':
      return true
    default:
      return false
  }
}

function writeBraceletInfo (info) {
  if (isWhiteBracelet(info.mac) || info.isBraceletWritten === 0 && !hasWritten[info.id]) {
    hasWritten[info.id] = true
    return new Promise(resolve => {
      EventBus.$emit(Event.WRITE_BRACELET, info.id)
      JsNativeApi.setBraceletUserInfo({
        id: info.key,
        name: info.name
      }).then(result => {
        resolve()
        if (hasChanged(info)) {
          log(`bracelet has changed, so stop mark. currMac is ${ currMac }, target is ${ info.mac }`)
          return
        }
        if (result) {
          http.post(info.isStaff ? 'staff/bracelet/mark' : 'parent/bracelet/mark', info.isStaff ? {
            token: info.token,
            macAddress: info.mac
          } : {
            idStudent: info.id,
            token: info.token,
            macAddress: info.mac
          }).then(() => {
            EventBus.$emit(Event.WRITE_BRACELET_SUCCESS, info.id)
          }, error => {
            EventBus.$emit(Event.WRITE_BRACELET_FAIL, {
              key: info.id,
              code: error.code,
              message: error.message
            })
          })
        } else {
          EventBus.$emit(Event.WRITE_BRACELET_FAIL, {
            key: info.id
          })
        }
      })
    })
  }
}

function checkSteps (info) {
  return new Promise(resolve => {
    let id = info.id
    if (!cache[id]) {
      cache[id] = { steps: 0 }
    }
    JsNativeApi.getBraceletHealthActivity().then(result => {
      resolve()
      if (hasChanged(info)) {
        log(`bracelet has changed, so stop update server steps. currMac is ${ currMac }, target is ${ info.mac }`)
        return
      }
      if (result.type === 'true') {
        let nativeSteps = Number(result.data.step || 0)
        if (nativeSteps === cache[id].steps) {
          log(`${ id } fetch same native steps: ${ nativeSteps }`)
          return
        }
        http.post(info.isStaff ? 'staff/sport' : 'parent/child/sport', info.isStaff ? {
          token: info.token
        } : {
          token: info.token,
          idStudent: id
        }, {
          ignore: true,
          prevent: true
        }).then(res => {
          let steps = res.data.steps || 0
          log(`${ id } fetch server steps: ${ steps }`)
          if (nativeSteps > steps) {
            http.post(info.isStaff ? 'staff/steps/update' : 'parent/child/steps/update', info.isStaff ? {
              token: info.token,
              steps: nativeSteps
            } : {
              token: info.token,
              idStudent: id,
              steps: nativeSteps
            }, {
              ignore: true,
              prevent: true
            }).then(() => {
              cache[id].steps = nativeSteps
              log(`${ id } update server steps finished: native > server`)
            }, error => {
              log(`${ id } update server steps failed: ${ error.message }`)
            })
          } else {
            log(`${ id } update server steps finished: native <= server`)
          }
        }, error => {
          log(`${ id } fetch server steps failed: ${ error.message }`)
        })
      } else {
        log(`${ id } fetch native steps failed`)
      }
    })
  })
}

function checkEnerge (info) {
  return new Promise(resolve => {
    JsNativeApi.getBraceletBaseInfo().then(result => {
      resolve()
      if (hasChanged(info)) {
        log(`bracelet has changed, so stop get energe. currMac is ${ currMac }, target is ${ info.mac }`)
        return
      }
      if (result.type === 'true') {
        currEnerge = result.data.energe
      } else {
        currEnerge = -1
      }
      EventBus.$emit(Event.BRACELET_ENERGE_CHANGED)
    })
  })
}

function createWrapper (info) {
  return function (handler) {
    return function () {
      if (hasChanged(info)) {
        throw 'break wrapper'
      }
      return handler(info)
    }
  }
}

function process (mac) {
  let info = findTarget(mac)
  if (info) {
    let wrapper = createWrapper(info)

    Promise.resolve()
      .then(wrapper(writeBraceletInfo))
      .then(wrapper(checkSteps))
      .then(wrapper(checkEnerge))
      .then(wrapper(() => {
        let timer = setInterval(() => {
          Promise.resolve()
            .then(wrapper(checkSteps))
            .then(wrapper(checkEnerge))
            .catch(e => {
              log(e)
              clearInterval(timer)
            })
        }, 600000)
      }))
      .catch(e => log(e))
  }
}

function setState (state, payload = null) {
  currState = state
  switch (state) {
    case State.CONNECTED:
      tempMac = null
      if (currMac !== payload) {
        unique += 1
        currMac = payload
        currEnerge = -1
        process(currMac)
      }
      break
    case State.CONNECTING:
      tempMac = payload
      break
    case State.WAITING:
    case State.UNINITIALIZED:
      unique += 1
      currMac = null
      tempMac = null
      currEnerge = -1
      break
    default:
      break
  }
  log(`Bracelet: set state to ${ state }, currMac is ${ currMac }, , tempMac is ${ tempMac }`)
  EventBus.$emit(Event.BRACELET_STATE_CHANGED)
}

function connectBracelet (mac) {
  setState(State.CONNECTING, mac)
  JsNativeApi.connectBracelet(mac).then(result => {
    if (!result && currState === State.CONNECTING) {
      setState(State.WAITING)
    }
  })
}

function disconnectBracelet (mac) {
  if (mac) {
    setState(State.DISCONNECTING)
    JsNativeApi.disConnectBracelet().then(result => {
      if (!result && currState === State.DISCONNECTING) {
        setState(State.CONNECTED, mac)
      }
    })
  }
}

function getBraceletState () {
  setState(State.SYNCING)
  JsNativeApi.isBraceletConnected().then(result => {
    if (currState === State.SYNCING) {
      if (result.type === 'true') {
        if (tempMac && tempMac !== result.mac) {
          disconnectBracelet(result.mac)
        } else {
          setState(State.CONNECTED, result.mac)
        }
      } else {
        if (tempMac) {
          connectBracelet(tempMac)
        } else {
          setState(State.UNINITIALIZED)
        }
      }
    }
  })
}

function isSleep () {
  return currState === State.UNINITIALIZED
}

export const methods = {
  connect (mac) {
    switch (true) {
      case currState === State.UNINITIALIZED:
        tempMac = mac
        if (JsNativeApi.isReady) {
          getBraceletState()
        }
        break
      case currState === State.WAITING:
      case currState === State.CONNECTING:
        connectBracelet(mac)
        break
      case currState === State.CONNECTED:
        tempMac = mac
        disconnectBracelet(currMac)
        break
      default:
        tempMac = mac
        break
    }
  },
  getStateInfo () {
    return {
      state: currState,
      connected: currMac,
      connecting: tempMac,
      energe: currEnerge
    }
  }
}

EventBus.$on(Event.LOGOUT, () => {
  setState(State.UNINITIALIZED)
})

EventBus.$on(SystemEvent.BRACELET_CONNECTED, data => {
  if (isSleep()) {
    return
  }
  if (data && data.macAddress) {
    if (currState === State.SYNCING && tempMac && tempMac !== data.macAddress) {
      disconnectBracelet(data.macAddress)
      return
    }
    setState(State.CONNECTED, data.macAddress)
  }
})

EventBus.$on(SystemEvent.BRACELET_DISCONNECT, () => {
  if (isSleep()) {
    return
  }
  if (currState === State.DISCONNECTING && tempMac) {
    currMac = null
    connectBracelet(tempMac)
  } else {
    setState(State.WAITING)
  }
})

EventBus.$on(SystemEvent.BRACELET_CONNECT_FAIL, () => {
  if (isSleep()) {
    return
  }
  if (currState === State.CONNECTING) {
    setState(State.WAITING)
  }
})

EventBus.$on(SystemEvent.NATIVE_READY, () => {
  if (currState === State.UNINITIALIZED && tempMac) {
    getBraceletState()
  }
})
