<template>
  <div id="app">
    <router-view/>
    <div v-if="isWritting" class="c-written-mask" @touchmove.stop.prevent="touchmove">
      <div v-if="writtenState === 0">
        <div class="c-written-loading">
          <img width="30" height="30" src="./components/loading.gif" />
          <div class="c-written-loading__tip">{{ $t('written.loading') }}</div>
        </div>
        <div class="c-written-cancel" @click="writtenState = 1">{{ $t('common.cancel') }}</div>
      </div>
      <div v-if="writtenState === 1" class="c-written-confirm">
        <div class="c-written-confirm__title">{{ $t('common.tip') }}</div>
        <div class="c-written-confirm__content">{{ $t('written.content') }}</div>
        <div class="c-written-confirm__wrapper">
          <div class="c-written-confirm__button--cancel" @click="writtenState = 0">{{ $t('common.cancel') }}</div>
          <div class="c-written-confirm__button--ok t-adapt-text" @click="isWritting = false">{{ $t('common.confirm') }}</div>
        </div>
      </div>
    </div>
    <div v-if="showMask" class="c-requesting-mask" @touchmove.stop.prevent>
      <div class="c-requesting-mask__wrapper">
        <img width="30" height="30" src="./components/loading.gif" />
        <div class="c-requesting-mask__label">{{ $t('loading') }}</div>
      </div>
    </div>
    <pop-alert v-if="mode" :title="$t('common.notice')" :tip="$t('error.token')" :button="$t('common.confirm')" @confirm="confirm"></pop-alert>
    <toast/>
    <div v-if="isForceUpgrade" class="c-requesting-mask" @touchmove.stop.prevent>
      <div class="c-requesting-mask__wrapper">
        <img width="30" height="30" src="./components/loading.gif" />
        <div class="c-requesting-mask__label">{{ $t('upgrade.upgrading') }}</div>
      </div>
    </div>
    <pop-alert v-if="needUpgrade" :title="$t('common.notice')" :tip="upgradeTip" :button="$t('common.confirm')" @confirm="shutdown"></pop-alert>
  </div>
</template>

<script>
import * as SystemEvent from '@/code/system-event.js'
import * as Event from '@/code/custom-event.js'
import popAlert from '@/components/PopAlert.vue'
import toast from '@/views/components/Toast.vue'

export default {
  name: 'app',
  components: {
    popAlert,
    toast
  },
  data () {
    return {
      mode: null,
      showMask: false,
      maskId: -1,
      isWritting: false,
      writtenState: 0,
      writtenKey: null,
      isForceUpgrade: false,
      needUpgrade: false,
      upgradeTip: ''
    }
  },
  methods: {
    confirm () {
      this.mode = null
    },
    shutdown () {
      this.$native.shutdownApp()
      this.needUpgrade = false
    }
  },
  created () {
    this.$eventBus.$on(Event.REQUESTING, id => {
      this.showMask = true
      this.maskId = id
    })
    this.$eventBus.$on(Event.REQUEST_FINISHED, id => {
      if (this.maskId === id) {
        this.maskId = -1
        this.showMask = false
      }
    })
    this.$eventBus.$on(Event.TOKEN_INVALID, mode => {
      this.$router.replace({ name: 'login', params: { mode } })
      this.mode = mode
    })
    this.$eventBus.$on(Event.WRITE_BRACELET, key => {
      this.isWritting = true
      this.writtenState = 0
      this.writtenKey = key
    })
    this.$eventBus.$on(Event.WRITE_BRACELET_SUCCESS, key => {
      if (key === this.writtenKey) {
        this.isWritting = false
        this.writtenKey = null
        this.$eventBus.$emit(Event.TOAST, {
          code: -1,
          message: this.$t('written.success')
        })
      }
    })
    this.$eventBus.$on(Event.WRITE_BRACELET_FAIL, error => {
      if (error.key === this.writtenKey) {
        this.isWritting = false
        this.writtenKey = null
        this.$eventBus.$emit(Event.TOAST, {
          code: error.code || -1,
          message: error.message || this.$t('written.fail')
        })
      }
    })
    this.$eventBus.$on(Event.LOGOUT, () => {
      this.isWritting = false
      this.writtenKey = null
    })
    this.$eventBus.$on(SystemEvent.VERSION_UPGRADE, () => {
      this.isForceUpgrade = false
      this.upgradeTip = this.$t('upgrade.tip')
      this.needUpgrade = true
    })
    this.$eventBus.$on(SystemEvent.VERSION_UPGRADE_FORCE, () => {
      this.isForceUpgrade = true
    })
    this.$eventBus.$on(SystemEvent.VERSION_UPGRADE_FAIL, code => {
      if (this.isForceUpgrade) {
        this.isForceUpgrade = false
        this.upgradeTip = this.$t(`upgrade.error.${ code }`)
        this.needUpgrade = true
      }
    })
    this.$eventBus.$on(SystemEvent.VERSION_UPGRADING, () => {
      this.$log('升级中...')
    })
    this.$eventBus.$on(SystemEvent.ANDROID_APP_WILL_EXIT, () => {
      this.$eventBus.$emit(Event.TOAST, {
        code: -1,
        message: this.$t('app.exit')
      })
    })
  },
  beforeMount () {
    let element = document.getElementById('loading')
    if (element) {
      document.body.removeChild(element)
    }
  }
}
</script>

