<template>
  <character-intro :person="intro" @avatar="clickAvatar">
    <div class="c-bracelet-state">
      <div class="c-bracelet-state__icon" :class="{ 'has-unbind': state === 0, 'has-unconnected': state === 1, 'has-connected': state === 2 }" @click="check"></div>
      <div v-if="energe >= 0" class="c-bracelet-state__energe" :class="energeClass"></div>
      <div class="c-bracelet-state__label" :class="{ 'is-bottom': energe >= 0 }">{{ stateTip }}</div>
    </div>
  </character-intro>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import { Gender } from '@/enum/common.js'
import * as Event from '@/code/custom-event.js'
import characterIntro from '@/views/components/CharacterIntro.vue'

export default {
  name: 'home-character-Intro',
  props: ['person', 'connected', 'connecting'],
  components: {
    characterIntro
  },
  data () {
    return {
      energe: -1
    }
  },
  computed: {
    ...mapState(['user']),
    ...mapGetters(['isStaff']),
    braceletMac () {
      if (this.person.braceletOfBinding === 1) {
        return this.isStaff ? this.person.macAddress : this.person.braceletMac
      }
      return ''
    },
    state () {
      switch (true) {
        case this.person.braceletOfBinding === 0:
          return 0
        case this.connected && this.connected === this.braceletMac:
          return 2
        case !!this.connecting:
          return -1
        default:
          return 1
      }
    },
    stateTip () {
      if (this.energe >= 0 && this.energe <= 10) {
        return this.$t('state.low')
      }
      switch (this.state) {
        case 0:
          return this.$t('state.unbind')
        case 1:
          return this.$t('state.unconnected')
        case 2:
          return this.$t('state.connected')
        default:
          return ''
      }
    },
    level () {
      switch (this.person.roles) {
        case 1:
        case 2:
        case 3:
        case 4:
          return this.$t('staffLevel.admin')
        case 5:
          return this.$t('staffLevel.headTeacher')
        case 6:
          return this.$t('staffLevel.teacher')
        case 7:
          return this.$t('staffLevel.staff')
        default:
          return this.$t('staffLevel.unknown')
      }
    },
    intro () {
      let name = this.person.name
      return {
        name: this.isStaff ? this.$t('home.staff.name', {
          name
        }) : name,
        headUrl: this.person.headUrlAttachmentId ?this.$transformHeadUrl(this.person.headUrlAttachmentId, this.user.token) : null,
        info: this.isStaff ? '' : this.$t('common.teacher') + this.person.headTeacherUserName,
        tag: this.isStaff ? this.level : this.$t('format.classInfo2', {
          gradeLevel: this.person.gradeLevel,
          classId: this.person.classId
        }),
        isKid: !this.isStaff,
        isMale: this.person.gender === Gender.MALE
      }
    },
    energeClass () {
      switch (true) {
        case this.energe > 80:
          return 'is-81-100'
        case this.energe > 60:
          return 'is-61-80'
        case this.energe > 40:
          return 'is-41-60'
        case this.energe > 20:
          return 'is-21-40'
        case this.energe > 10:
          return 'is-11-20'
        default:
          return 'is-low'
      }
    }
  },
  watch: {
    state: {
      handler: function () {
        this.checkEnerge()
      },
      immediate: true
    }
  },
  methods: {
    clickAvatar () {
      if (this.isStaff) {
        this.$router.push({ name: 'account' })
      } else {
        this.$router.push({ name: 'children-info', params: { id: this.person.id } })
      }
    },
    check () {
      if (this.state === 0) {
        this.$router.push({ name: 'bracelet' })
      }
    },
    checkEnerge () {
      if (this.state === 2) {
        let info = this.$bracelet.getStateInfo()
        this.energe = info.energe
      } else {
        this.energe = -1
      }
    }
  },
  created () {
    this.$eventBus.$on(Event.BRACELET_ENERGE_CHANGED, this.checkEnerge)
  },
  beforeDestroy () {
    this.$eventBus.$off(Event.BRACELET_ENERGE_CHANGED, this.checkEnerge)
  }
}
</script>

<style lang="scss">
.c-bracelet-state {
  position: relative;
  width: 86px;
  padding-top: 6px;
  text-align: center;
  &__icon {
    position: relative;
    display: inline-block;
    width: 40px;
    height: 50px;
    @include bg-img-ratio('../../assets/sh_icon', 64, 100);
    &::after {
      content: '';
      position: absolute;
      right: 1px;
      bottom: 6px;
      @include bg-img-ratio('../../assets/loading', 28, 28);
    }
    &.has-unbind {
      @include bg-img-ratio('../../assets/unbound', 93, 94);
      &::after {
        display: none;
      }
    }
    &.has-connected::after {
      right: 0;
      bottom: 3px;
      @include bg-img-ratio('../../assets/connect', 35, 36);
    }
    &.has-unconnected::after {
      right: 0;
      bottom: 3px;
      @include bg-img-ratio('../../assets/unconnect', 35, 36);
    }
  }
  &__energe {
    position: absolute;
    top: 66px;
    left: 50%;
    transform: translateX(-50%);
    margin-left: -2px;
    @include bg-img-attrs(37, 20);
    &.is-low {
      @include bg-img-ratio-normal('../../assets/1%_10%');
    }
    &.is-11-20 {
      @include bg-img-ratio-normal('../../assets/11%_20%');
    }
    &.is-21-40 {
      @include bg-img-ratio-normal('../../assets/21%_40%');
    }
    &.is-41-60 {
      @include bg-img-ratio-normal('../../assets/41%_60%');
    }
    &.is-61-80 {
      @include bg-img-ratio-normal('../../assets/61%_80%');
    }
    &.is-81-100 {
      @include bg-img-ratio-normal('../../assets/81%_100%');
    }
  }
  &__label {
    position: absolute;
    top: 72px;
    left: 50%;
    transform: translateX(-50%);
    text-align: center;
    color: rgba(#fff, 0.8);
    font-size: 12px;
    white-space: nowrap;
    @extend %font-family__sf;
    &.is-bottom {
      top: 82px;
    }
  }
}
</style>
